import React from 'react'
import { Link } from 'gatsby'
import { ModalRoutingContext } from 'gatsby-plugin-modal-routing'
import "../styles/styles.css"
import image1 from "../../static/MaskOff/Image_1.png"
import image2 from "../../static/MaskOff/Image_2.png"
import image3 from "../../static/MaskOff/Image_3.png"
import image4 from "../../static/MaskOff/Image_4.png"
import image5 from "../../static/MaskOff/Image_5.png"
import image6 from "../../static/MaskOff/Image_6.png"


const ModalExamplePage = () => (
  <ModalRoutingContext.Consumer>
    {({ modal, closeTo }) => (
      <div>
        {modal ? (
          <Link to={closeTo} class="close">
            Close
          </Link>
        ) : (
          <header>
            <h1>
              Website Title
            </h1>
          </header>
        )}

        <h1>Mask Off</h1>
        
        <img class="maskoff" src={image1} alt="image1"/>
        <img class="maskoff" src={image2} alt="image2"/>
        <img class="maskoff" src={image3} alt="image3"/>
        <img class="maskoff" src={image4} alt="image4"/>
        <img class="maskoff" src={image5} alt="image5"/>
        <img class="maskoff" src={image5} alt="image6"/>

        <div>
          <p>
          Game made for the Global Game Jam 2026 with the theme Mask. The game was made in a team of 3 people, me as the programmer, 1 sound designer + game designer and 1 artist.
          The game randomly generates a murder location and a murder time. From a list of randomly generated masks you need to analise their text and determine which one of the is the murderer.
          You can assign tags to them which will be visible in a subsequent screen that has files for each mask containing everything you know.
          <br></br>
          Try finding the murderer in as few guesses as possible before all of the masks die and you lose.
          <br></br>
          The game is made in Unity using C#, made during the 48 hour period of the game jam.
          <br></br>
          GGJ Link: <a href="https://globalgamejam.org/games/2026/play-words-6"> The games page.</a>
          <br></br>          
          Itch.io Link: <a href="https://coninstar.itch.io/mask-off"> The games page.</a>
          </p>
        </div>
      </div>
    )}
  </ModalRoutingContext.Consumer>
)

export default ModalExamplePage